<script>
  import NodeChild from './VnodeChild'
  export default {
    data (){
      return {
        c: true,
        childShow: true
      }
    },
    computed: {
      childShowSays(){
        return this.childShow;
      }
    },
    methods: {
      clickHandler (){
        alert('hey')
      },
      addClassToDom(){
        this.$children[ 0 ].$vnode.data.class['red'] = true;
        this.$children[ 0 ].$forceUpdate();
      },
      addClassToEl(){
        document.querySelector('.node').classList.add('green');
      },
      childToggle (){
        this.childShow= !this.childShow;
      },
      innerWhole (){
        innerWhole()
      },
      innerEach(){
        innerEach()
      },
      addingChild (){
        this.cc= 10;
      }
    },
    components: {
      'node-child': NodeChild
    },
    mounted(){
    }
  }
  function innerWhole () {
    let el = document.querySelector('.flick-panel')
    let inner_html = el.innerHTML;

    console.time('a')
    _innerWhole(el, inner_html, 100)
  }
  function _innerWhole (el, inner_html, i) {
    el.innerHTML = inner_html;
    if ( i < 100 ) {
      _innerWhole(el, inner_html, ++i)
    } else {
      console.timeEnd('a')
    }
  }
  function innerEach () {
    var el = document.querySelector('.api_list')
    var children = [].slice.call(el.children);
    var inner_html = children.map(function (v) {
      return v.innerHTML
    })
    var len = children.length;

    console.time('b')
    _innerEach(children, inner_html, len, 100)
  }

  function _innerEach (children, inner_html, len, i) {
    for ( let j = 0; j < len; j++ ) {
      children[ j ].innerHTML = inner_html[ j ]
    }
    if ( i < 100 ) {
      _innerEach(children, inner_html, len, ++i)
    } else {
      console.timeEnd('b')
    }
  }

</script>
